<template>
  <div>
    <div class="demo-image">
      <div  align="center" class="block" v-for="fit in fits" :key="fit">
        <el-image
                style="width: 1000px; height: 300px"
                :src="url"
                :fit="fit"></el-image>
      </div>
    </div>
    <div id="showmsg" align="center">
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="公告内容">
          <el-input
                  type="textarea"
                  autosize
                  placeholder="请输入内容"
                  v-model="form.desc">
          </el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">立即创建</el-button>
          <el-button>取消</el-button>
        </el-form-item>
      </el-form>
    </div>

  </div>
</template>

<script>

    import {netCreMsg} from "../../network/optfile";
    export default {
        name: "insertMsg",
        data() {
            return {
                tableData: [],
                fits: ['fill'],
                url: require('@/assets/img/timg.gif'),
                form: {
                    desc: ''
                }
            }
        },
        methods: {
            onSubmit() {
                if (this.$store.getters.curOptUser==''){
                    this.$router.replace('/login');
                    return
                }
                if (this.form.desc == '') {
                    this.openfaild()
                    return
                }
                let obj = {}
                obj.name = this.$store.getters.curOptUser.name
                obj.email = this.$store.getters.curOptUser.email
                obj.message = this.form.desc

                netCreMsg(obj).then(res => {
                    this.open()
                })
            },
            openfaild() {
                this.$alert('失败,无内容', '创建提示', {
                    confirmButtonText: '确定',
                    callback: action => {
                        this.$message({
                            type: 'info',
                            message: `创建失败`
                        });
                    }
                });
            },
            open() {
                this.$alert('成功', '创建提示', {
                    confirmButtonText: '确定',
                    callback: action => {
                        this.$message({
                            type: 'info',
                            message: `执行成功`
                        });
                    }
                });
            }
        }
    }
</script>

<style scoped>

  #showmsg{
    margin: 20px auto;
    width: 1000px;
  }

</style>